body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

img{
  max-width: 100vw;
  max-height: 100vh;
  width:  auto;
  height: auto;
  margin: auto;
  display: block;
}
figure{
  margin: 0;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.headerBG{
    background: url('https://i.imgur.com/B0J7mgo.jpg') no-repeat center;
    background-size: cover;
    height: 100vh;
}
.headerTitle{
    color: #ffffff !important;
    top: 0;
    bottom: 0;
    display: block;
    margin: auto;
}
.headerGD{
    height: 100vh;
}
.fluctlight{
    display: inline-block;
    border-right: #ffffff 0.2rem solid;
    -webkit-animation: FluctLight 1s ease-in-out infinite;
            animation: FluctLight 1s ease-in-out infinite;
    transition: 1s;
}
@-webkit-keyframes FluctLight{
    0%{
        border-right: #ffffff 0.2rem solid;
    }
    50%{
        border-right: rgba(0, 0, 0, 0) 0.2rem solid;
    }
    100%{
        border-right: #ffffff 0.2rem solid;
    }
}
@keyframes FluctLight{
    0%{
        border-right: #ffffff 0.2rem solid;
    }
    50%{
        border-right: rgba(0, 0, 0, 0) 0.2rem solid;
    }
    100%{
        border-right: #ffffff 0.2rem solid;
    }
}
